export default {
  data: function data() {
    return {
      dialog: false,
      drawer: null,
      dataSocket: {},
      scopeAdmin: false
    };
  },
  props: {
    tableName: String
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      try {
        var isAdmin = vm.getUser('Administrator');

        if (scopeAdminConfig && !isAdmin) {
          vm.scopeAdmin = true;
        }
      } catch (error) {}
    });
  },
  computed: {
    items: function items() {
      return this.$store.getters.getlistTableMenu;
    },
    loginUser: function loginUser() {
      return this.$store.getters.getloginUser;
    },
    userRoles: function userRoles() {
      return this.$store.getters.getUserRoles;
    },
    snackbarerror: {
      // getter
      get: function get() {
        return this.$store.getters.getsnackbarerror;
      },
      // setter
      set: function set(newValue) {
        this.$store.commit('setsnackbarerror', newValue);
      }
    },
    snackbarsocket: {
      // getter
      get: function get() {
        return this.$store.getters.getsnackbarsocket;
      },
      // setter
      set: function set(newValue) {
        this.$store.commit('setsnackbarsocket', newValue);
      }
    }
  },
  watch: {
    userRoles: function userRoles() {
      var vm = this;

      try {
        var isAdmin = vm.getUser('Administrator');

        if (scopeAdminConfig && !isAdmin) {
          vm.scopeAdmin = true;
        } else {
          vm.scopeAdmin = false;
        }
      } catch (error) {
        vm.scopeAdmin = false;
      }
    }
  },
  methods: {
    getUser: function getUser(roleItem) {
      var vm = this;
      var roles = vm.userRoles;

      if (!roles) {
        return false;
      }

      var roleExits = roles.findIndex(function (item) {
        return item.role === roleItem;
      });
      return roleExits >= 0;
    },
    redirectFilter: function redirectFilter(val) {
      this.$router.push(val + '?state_change=' + Math.floor(Math.random() * (100 - 1 + 1)) + 1);
    },
    closeError: function closeError() {
      this.$store.commit('setsnackbarerror', false);
    },
    reloadPage: function reloadPage() {
      window.location.reload(true);
    },
    redirectControlPanel: function redirectControlPanel() {
      var controlPanelURL = '';

      if (window.themeDisplay !== null && window.themeDisplay !== undefined) {
        controlPanelURL = window.themeDisplay.getLayoutRelativeControlPanelURL();
      }

      window.location.href = controlPanelURL + '?p_p_id=com_liferay_layout_admin_web_portlet_GroupPagesPortlet';
    },
    getItemSearch: function getItemSearch() {
      var vm = this;

      if (vm.items.length === 2) {
        return vm.items[1].children;
      } else if (vm.items.length > 2) {
        return vm.items[2].children;
      } else {
        return [];
      }
    },
    doLogOut: function doLogOut() {
      window.location.href = '/c/portal/logout';
    },
    thongKeTruyCap: function thongKeTruyCap() {
      // window.location.href = 'https://analytics.google.com'
      this.$router.push({
        name: 'ThongKeTruyCap'
      });
    }
  }
};